import axios from "axios";
const useFakeData = true;

const fetchQuestions = () => new Promise((resolve, reject) => {
  if (useFakeData) {
    resolve([
      {
        question: "Hr er textinn me spurningunni, hann er nokku langur og fer byggilega  nokkrar lnur og brtur alla hnnun.",
        answers: [
          {
            text: "Svar 1, a er nokku langt og brotnar  ara lnu v a a er alveg rosalega langt",
            scores: [
              { name: "Byggingatkni", score: 3 },
              { name: "Vefsklinn", score: 2 },
              { name: "Tlvubraut", score: 1 },
            ],
          },
          {
            text: "Svar 1, a er nokku langt og brotnar  ara lnu",
            scores: [
              { name: "Byggingatkni", score: 3 },
              { name: "Vefsklinn", score: 2 },
              { name: "Tlvubraut", score: 1 },
            ],
          },
          {
            text: "Svar 1, a er nokku langt og brotnar  ara lnu",
            scores: [
              { name: "Byggingatkni", score: 3 },
              { name: "Vefsklinn", score: 2 },
              { name: "Tlvubraut", score: 1 },
            ],
          },
          {
            text: "Svar 1, a er nokku langt og brotnar  ara lnu",
            scores: [
              { name: "Byggingatkni", score: 3 },
              { name: "Vefsklinn", score: 2 },
              { name: "Tlvubraut", score: 1 },
            ],
          },
        ],
      },
    ]);
  } else {
    axios.get("http://framaprof.is/api/")
      .then(({ data }) => resolve(data.results))
      .catch(err => reject(err));
  }
});

export default fetchQuestions;



